/* STATIC STYLES (desktop-first) */

.experience {
  display: flex;
  flex-direction: column;

  gap: 5rem;
}

.experience-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  gap: 2rem;
}

.experience-cards > * {
  flex: 0 0 20rem;
}

.experience-card {
  text-align: center;
}

.experience-card-header {
  margin-bottom: 1rem;

  background: linear-gradient(90deg, var(--color-primary-text-dark), var(--color-secondary));
  background-clip: text;

  -webkit-text-fill-color: transparent;
}

.experience-card-inner {
  display: inline-block;

  text-align: left;
}

.experience-card-inner p {
  display: flex;
  align-items: center;

  gap: 0.75rem;
  margin-block: 0.3rem;
}

.experience-balls {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;

  gap: 2rem;
}

.experience-balls > div {
  width: 8rem !important;
  height: 8rem !important;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
}

@media (max-width: 992px) {
}

@media (max-width: 768px) {
  .experience {
    padding-inline: 8rem;
  }
}

@media (max-width: 576px) {
  .experience {
    padding-inline: 2rem;
  }

  .experience-cards {
    flex-direction: column;
  }

  .experience-cards > * {
    flex: 1;
  }

  .experience-balls {
    display: none;
  }
}
